    document.getElementById("hw6.1").addEventListener("click", function() {


    let shoppingList = [
        {
            name : "apples",
            quantity : 2,
            buy : false
        },

        {
            name : "oranges",
            quantity : 3,
            buy : false
        },

        {
            name : "bananas",
            quantity : 4,
            buy : false
        },

        {
            name : "tomato",
            quantity : 4,
            buy : false
        }
    ];


    const showShoppingList = (arr) => {

        arr.sort((a,b) => {
            return a.buy - b.buy;
        });

        arr.forEach( (item, i, arr) => {
            console.log(`${i+1}.\t  : ${item.name} \t \t  -: ${item.quantity} \t : ${item.buy}`);
        });
    }

    const addProductsInList = (product) => {



        let newObg = {};

        for( let key in shoppingList){
        
            if (shoppingList[key].name === product) {
                shoppingList[key].quantity += 1;
                
                return ` ${product} -   "`;;
            }
        }

        newObg["name"] = product;
        newObg["quantity"] = 1;
        newObg["buy"] = false;
        shoppingList.push(newObg);
        return ` ${product} -   ""`;;
    
    }

    const markProductBought = (product) => {
        
        for( let key in shoppingList){
        
            if (shoppingList[key].name === product ) {
            
                if(shoppingList[key].buy === false){

                    shoppingList[key].buy = true;
                    return ` ${product} -   ""`;
                }

                else if (shoppingList[key].buy === true){
                    //console.log( ` ${product} -  `);
                    return ` ${product} -  `;
                }    
                
            }
        
        }
    }


    let answer = Number(prompt(`  ! \n  : \n 1 -   \n 2 -     \n 3 -  `,''));

    while(answer) {

        switch(answer) {
            case 1: {
                console.log(`\n  `);
                showShoppingList(shoppingList);
                console.log(`\n`);
                break;
            }
            case 2: {
                const product = prompt("  :", '');
                addProductsInList(product);
                console.log(` "${product}"   `);
                break;
            }
            case 3: {
                const productBought = prompt("  :", '')
                console.log(markProductBought(productBought));
                break;
            }
        }
            
        answer = Number(prompt(` : \n 1 -   \n 2 -     \n 3 -  `,''));
    }

    
});

