    document.getElementById("hw6.2").addEventListener("click", function() {

    let check = [
        {
            name: "",
            quantity : 2,
            price : 300
        },

        {
            name: "",
            quantity : 1,
            price : 150
        },
        {
            name: "",
            quantity : 1,
            price : 5
        }
    ];

    const showCheck = (arr) => {
        console.log(`  : `);
        arr.forEach( (item, i, arr) => {
            console.log(`${i+1}.\t ${item.name} \t \t  -: x ${item.quantity} \t  ${item.price * item.quantity} `);
        });
    };

    const calcSum = (arr) => {
        let sum = 0;
        arr.forEach( (item, i, arr) => {
            sum += item.price;
        });
        
        return sum;

    };

    const calcBiggestPurchase = (arr) => {
        let maxCost = 0;
        arr.forEach( (item, i, arr) => {
            if(item.price > maxCost) {
                maxCost = item.price;
            console.log( `   : ${item.name} - ${maxCost} `);   
            }
        });
        
    };

    const calcAvarageCalc = (arr) => {
        const amountSales = calcSum(arr);

        let sumProducts = 0;
        arr.forEach( (item, i, arr) => {
            sumProducts += item.quantity;
        });
        return (amountSales / sumProducts).toFixed(2);
    }


    showCheck(check);
    calcSum(check);
    calcBiggestPurchase(check);
    calcAvarageCalc(check);
    console.log(`      : ${calcAvarageCalc(check)} `);
    alert(`  !`);
});


